import React from 'react';
import css from '@emotion/css';
import styled from '@emotion/styled';

interface ISlider {
  checked?: boolean;
}

interface IToggle extends ISlider {
  onChange?: () => void;
}

const Toggle: React.FC<IToggle> = ({ checked = false, onChange }) => {
  return (
    <Container onClick={onChange}>
      <Slider checked={checked} />
    </Container>
  );
};

export default Toggle;

const Container = styled.div`
  width: 40px;
  height: 20px;
  border-radius: 10px;
  background-color: #d7d7d7;
  position: relative;
  html[color-mode='dark'] & {
    background-color: #4b4c4e;
  }
`;

const Slider = styled.div<ISlider>`
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: solid 7px #8c95a7;
  background-color: #30343e;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -2.5px;
  cursor: pointer;
  transition: 0.4s;

  ${({ checked }) =>
    checked &&
    css`
      transform: translateX(20px);
      border: 0;
      background: #0a62ba;
      html[color-mode='dark'] & {
        background: #295396;
      }
    `};
`;
