import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import { useHistory } from 'react-router';

import ServiceWrapper from '../components/ServiceWrapper';
import {
  ContentsWrap,
  Title,
  Box,
  InfoWrap,
  InfoRow,
  InfoTitle,
  InfoText,
  Line,
  BottomWrap,
  LeftWrap,
  StyledLink,
  DeleteAccountButton,
} from '../styles/pages/Setting.style';
import Modal from '../components/Modal';
import Input from '../components/Input';
import Toggle from '../components/Toggle';

import { api } from '../utils/api';

const Setting = () => {
  const history = useHistory();
  const [isOpenDeleteAccount, setIsOpenDeleteAccount] = useState(false);
  const [isNotificationsEnabled, setIsNotificationsEnabled] = useState(false);
  const [isDarkModeEnabled, setIsDarkModeEnabled] = useState(false);
  const [isViewSpeed, setIsViewSpeed] = useState(false);
  const [isSettingLoaded, setIsSettingLoaded] = useState(false);
  const [drawalReason, setDrawalReason] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      const {
        data: { data },
      } = await api.get('/setting');
      console.log(data);
      setIsNotificationsEnabled(data.is_receive_alarm ? true : false);
      setIsDarkModeEnabled(data.darkmode ? true : false);
      setIsViewSpeed(data.view_speed ? true : false);
      setIsSettingLoaded(true);
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      await api.post('/setting', {
        is_receive_alarm: isNotificationsEnabled ? 1 : 0,
        darkmode: isDarkModeEnabled ? 1 : 0,
        view_speed: isViewSpeed ? 1 : 0,
      });
      Object(document.querySelector('html')).setAttribute(
        'color-mode',
        isDarkModeEnabled ? 'dark' : 'light',
      );
    };
    if (isSettingLoaded) {
      fetchData();
    }
    // eslint-disable-next-line
  }, [isNotificationsEnabled, isDarkModeEnabled, isViewSpeed]);

  const handleDrawal = async () => {
    await api.post('/withdrawal', { reason: drawalReason });
    history.push('/auth/login');
  };

  return (
    <>
      <ServiceWrapper>
        <ContentsWrap>
          <Title>설정</Title>
          <Box>
            <InfoWrap>
              <InfoRow>
                <InfoTitle>버전</InfoTitle>
                <InfoText>v1.205.12346</InfoText>
              </InfoRow>
              <InfoRow>
                <InfoTitle>알림 수신</InfoTitle>
                <Toggle
                  checked={isNotificationsEnabled}
                  onChange={() =>
                    setIsNotificationsEnabled(!isNotificationsEnabled)
                  }
                />
              </InfoRow>
              <InfoRow>
                <InfoTitle>다크모드</InfoTitle>
                <Toggle
                  checked={isDarkModeEnabled}
                  onChange={() => setIsDarkModeEnabled(!isDarkModeEnabled)}
                />
              </InfoRow>
              <InfoRow>
                <InfoTitle>재생속도 표시</InfoTitle>
                <Toggle
                  checked={isViewSpeed}
                  onChange={() => setIsViewSpeed(!isViewSpeed)}
                />
              </InfoRow>
            </InfoWrap>
            <Line />
            <BottomWrap>
              <LeftWrap>
                <StyledLink to="/policy/service">이용약관 안내</StyledLink>
                <StyledLink to="/policy/privacy">개인정보처리방침</StyledLink>
              </LeftWrap>
              <DeleteAccountButton onClick={() => setIsOpenDeleteAccount(true)}>
                회원탈퇴
              </DeleteAccountButton>
            </BottomWrap>
          </Box>
        </ContentsWrap>
      </ServiceWrapper>
      <Modal
        open={isOpenDeleteAccount}
        onClose={() => setIsOpenDeleteAccount(false)}
        title="회원 탈퇴하기"
        content={
          <>
            회원을 탈퇴하시면 이용중이던 서비스를
            <br />더 이상 이용하실 수 없습니다.
            <StyledInput
              icon="edit"
              placeholder="간단한 사유를 적어주세요."
              value={drawalReason}
              onChange={(e) => setDrawalReason(e.target.value)}
            />
          </>
        }
        firstButtonLabel="회원 탈퇴"
        onClickFirstButton={handleDrawal}
        secondButtonLabel="취소"
        onClickSecondButton={() => setIsOpenDeleteAccount(false)}
      />
    </>
  );
};

const StyledInput = styled(Input)`
  margin-top: 30px;
`;

export default Setting;
